:root {
  --primary-font: DM Sans, sans-serif;
  --primary-text-black: #000;
  --grey-text-color: #907e82;
  --white-text-color: #fff;
  --red-text-color: #d41443;
  --main-bgc: #fff;
  --secondary-bgc: #fff5f6;
  --social-icon-bgc: #fea5bb;
  --accent-color: #6a2034;
  --timing-bezier: cubic-bezier(.4, 0, .2, 1);
  --transition-duration: 250ms;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

